import logo from './digitala.svg';
import './App.css';

function App() {
  function geoFindMe() {
    const status = document.querySelector('#status')
    const mapLink = document.querySelector('#map-link')

    mapLink.href = '';
    mapLink.textContent = '';

    const navigatorLocationOptions = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };

    function success(position) {
      const latitude = position.coords.latitude;
      const longitude = position.coords.longitude;
      status.textContent = '';
      mapLink.href = `https://www.openstreetmap.org/#map=19/${latitude}/${longitude}`;
      mapLink.textContent = `Latitude: ${latitude} °, Longitude: ${longitude} °`;

    }

    function error(e) {
      status.textContent = `Desabilitado para obtener la ubicación code: ${e.code} message: ${e.message}`;
    }

    if (!navigator.geolocation) {
      status.textContent = 'Geolocation is not supported by your browser';
    } else {
      status.textContent = 'Ubicando…';
      navigator.geolocation.getCurrentPosition(success, error, navigatorLocationOptions);
    }
  }

  return (
    <div className="App">
      <header className="App-header">

        <a
          className="App-link"
          href="https://digitala.cl"
          target="_blank"
          rel="noopener noreferrer"
        >
          {/* Digítala */}
          <img src={logo} className="App-logo" alt="logo" />
        </a>
      </header>
      <section className="geolocation">
        <button id="find-me" onClick={geoFindMe} >Muestrame mi Ubicación</button>
        <p id="status"></p>
        <a id="map-link" target="_blank">link</a>

      </section>
    </div>
  );
}

export default App;
